<template>
	<div id="admin">
		<header>
			<div class="btn-logout" @click="logout">
					<span class="logout-icon"></span>
					<span>退出</span>
			</div>
			<div class="header-user-info">
				<span class="user-icon"></span>
				<span>{{userInfo.user_name}}</span>
			</div>
		</header>
		<section class="container">
			 <aside class="aside">
				<button id="btn-toggle" :class="isCollapse?'down':'up'" @click="toggleCollapse"></button>
				<div class="leftMenu">
					<el-menu default-active="1-4-1" class="el-menu-vertical-demo" 
					@open="handleOpen" @close="handleClose" :collapse="isCollapse"
					background-color="#1f416c"
					text-color="#fff"
					active-text-color="#ffd04b"
					router 
      		:default-active="$route.path">
					<template v-for="(item,index) in asideMenuList">
            <template v-if="!item.children">
              <el-menu-item :index="item.url">
                <i :class="[classA,classB,item.icon]"></i>
								<template v-if="item.target==='_blank'">
									<router-link  target="_blank" :to="item.url" style="font-size:14px">{{item.pri_name}}</router-link>
								</template>
								<template v-else>
									<span slot="title" style="font-size:16px">{{item.pri_name}}</span>
								</template>
            	</el-menu-item>
            </template>
        		<template v-else>
							<el-submenu :index='getIndex(index)' class="subMenu">
							<template slot="title">
									<i :class="[classA,classB,item.icon]"></i>
									<span slot="title" style="font-size:16px">{{item.pri_name}}</span>
							</template>
							<template v-for="itemlist in item.children">
									<template   v-if="itemlist.target==='_blank'">
										<el-menu-item index="" >
										<i class="fa fa-circle"></i>
										<router-link  target="_blank" :to="itemlist.url" style="font-size:14px">{{itemlist.pri_name}}</router-link>
										</el-menu-item>
									</template>
									<template  v-else>
										<el-menu-item :index="itemlist.url" >
										<i class="fa fa-circle"></i>
										<span slot="title" style="font-size:14px">{{itemlist.pri_name}}</span>
										</el-menu-item>
									</template>
							</template>
							</el-submenu>
            </template>             
        	</template>			
				</el-menu>
				</div>
			</aside>
			<section class="main">
				  <el-breadcrumb separator-class="el-icon-arrow-right">
					<el-breadcrumb-item :to="{path:'/'}">首页</el-breadcrumb-item>
				
							<el-breadcrumb-item  v-for="(item,index) in $route.meta.remark" :key="index">{{item}}</el-breadcrumb-item>
				
				
				  </el-breadcrumb>
				<router-view></router-view>
			</section>
		</section>

		
	</div>
</template>

<script>
import deep from '@/js/deepclone';
import {Ajax} from '@/js/ajax'
import { setTimeout } from 'timers';
export default {
	data() {
      return {
				isCollapse: false,
				asideMenuList:[],
				classA:"fa",
				classB:'iconFa',
				userInfo:this.$store.getters.userInfo
      };
    },

	created () {
	
		let asideMenuList=deep.deepClone(this.$store.getters.asideMenuList);
		this.asideMenuList=this.formateTree(asideMenuList,null)
		console.log(this.asideMenuList);

		
	},
	mounted () {
			window.onresize=()=>{
				let mainObj = document.getElementsByClassName('main')[0];
				if(mainObj){
					if(this.isCollapse){
					if(window.innerWidth>1400){
						mainObj.style.marginLeft='50px'
					}else{
						mainObj.style.marginLeft='40px'
					}	
				}else{
					if(window.innerWidth>1400){
							mainObj.style.marginLeft='200px'
					}else{
						mainObj.style.marginLeft='180px'
					}
				}
				}
				
				// console.log('aaa')
			}
			// this.$socket.emit('my_event', {data: 'I\'m connected!'}); 
	},
	methods: {
		fun1(){
			// this.$socket.emit('my_event', {data: 'I\'m connected!'}); 
		
		},
		toggleCollapse(){
			this.isCollapse=!this.isCollapse
			this.setMainLeft(this.isCollapse);
		},
		handleOpen(key, keyPath) {
			console.log(key, keyPath);
		
		},
		handleClose(key, keyPath) {
			console.log(key, keyPath);
		},
		setMainLeft(isCollapse){
				let mainObj = document.getElementsByClassName('main')[0];
				if(mainObj){
					if(isCollapse){
						if(window.innerWidth>1400){
							mainObj.style.marginLeft='50px'
						}else{
							mainObj.style.marginLeft='40px'
						}	
					}else{
						if(window.innerWidth>1400){
			
								mainObj.style.marginLeft='200px'
						}else{
							mainObj.style.marginLeft='180px'
						}
					}
				}
			
				// console.log(window.innerWidth);
		},
		formateTree(data, parent_code) {
        var tree = [];
        var temp;
        for (let i = 0; i < data.length; i++) {
            if (data[i].parent_code == parent_code) {
                var obj = data[i];
                temp =this.formateTree(data, data[i].pri_code);
                if (temp.length > 0) {
                 obj.children = temp;
                }
                tree.push(obj);
            }
        }
        return tree;
		},
		getIndex(index){
			index=String(index);
			return index;
    },
		logout(){
			let that=this;
      this.$confirm('用户将退出登录，请确认是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'info',
        center:true,
      }).then(() => {
						Ajax({
							method:'post',
							url : httpConfig+'logout',
							data : {},
							done : function(error,data,resdata) {
								if(error){
									that.$message({
										showClose: true,
										message: error.msg,
										type: 'error'
									});
								}else{
									that.$message({
										type: 'success',
										message: '退出成功!'
									});
									that.$router.push('/login');
								}	
							},function(error) {
									that.$message({
										showClose: true,
										message: error.msg,
										type: 'error'
									});
							},					
						})
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消退出'
        });          
      });	
		}
	},
	//  sockets:{
  //       connect:function() {
	// 				console.log('lalala')
				
  //       },
  //       my_response(msg) {  
  //           console.log(msg,"；socket消息");
            
  //       }
       
	// 	},
	
}
</script>
<style lang="scss" src="@/styles/admin.scss" scoped>

</style>
<style >
.el-submenu__title{
	height: 50px;/*no*/
	line-height: 50px;/*no*/
}
 .submenu  .el-menu .el-menu-item{
	 width: 200px;/*no*/
 }
</style>








